<template>
  <div class="settings">
    <p>Settings are automatically saved.</p>
    <p>
      <input type="radio" id="easy" value="45" v-model="difficulty" />
      <label for="easy">Easy</label>
    </p>
    <p>
      <input type="radio" id="normal" value="35" v-model="difficulty" />
      <label for="normal">Normal</label>
    </p>
    <p>
      <input type="radio" id="hard" value="28" v-model="difficulty" />
      <label for="hard">Hard</label>
    </p>
    <p>
      <button class="primary" @click="newGame()">Generate</button>
      <button @click="close()">Cancel</button>
    </p>
  </div>
</template>

<script>
import { mapActions } from "vuex";

import { SudokuGenerator } from "@/helpers";

export default {
  name: "NewGame",
  data: function () {
    return { difficulty: 1 };
  },
  computed: {},
  methods: {
    ...mapActions("root", ["hideBackgroundAction"]),
    ...mapActions("sudokuStrings", ["newGridAction", "setInitialAction"]),
    ...mapActions("clues", ["clearAllClues"]),
    ...mapActions("highlight", ["updateIndexes"]),
    close() {
      this.hideBackgroundAction();
      this.$emit("close");
    },
    newGame: function () {
      let sudokuBoard = new SudokuGenerator(35).generate().getBoard(); //generate({ method: "generate" }).result.join("");
      sudokuBoard = sudokuBoard.map((el) => (el === "" ? "." : el));

      this.newGridAction(); // clear the grid
      this.clearAllClues(); // clear the clues
      this.setInitialAction(sudokuBoard.join("")); // set the new sudokustring
      this.updateIndexes(); // update all highlights
      this.close();
    },
  },
};
</script>

